.App{
    width: 50%;
    margin: auto;
    display: flex;
    flex-direction: column;
    font-size: 15px;
    font-weight: bold;
    font-family:Arial;
    
}
li{
    list-style-type:decimal;
    display: flex;
    background-color: #73b9ff;
    width: 98%;
    padding: 5px 5px;
    border:0 1px 0 1px solid white;
    
    
}
.header{
    border-bottom: 5px solid white;
    border-radius: 5px;
}
.data{
    width: 80%;
    height:40px;
    display: flex;
    align-items: center;
}
form{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px 0;
}
label,input{
    margin: 10px 0;
}
input{
    width: 98%;
    height: 40px;
   
}
input::placeholder{
    padding: 0 5px;
}
.add-btn{
    font-size:15px;
  font-family:Arial;
  width:20%;
  height:40px;
  border-width:1px;
  color:#ffffff;
  border-color:#337fed;
  font-weight:bold;
  border-top-left-radius:6px;
  border-top-right-radius:6px;
  border-bottom-left-radius:6px;
  border-bottom-right-radius:6px;
  box-shadow:inset 0px 1px 0px 0px #97c4fe;
  text-shadow:inset 0px 1px 0px #1570cd;
  background:linear-gradient(#3d94f6, #1e62d0);
  
}
.add-btn:hover{
    background: linear-gradient(#1e62d0, #3d94f6);
}
.del-btn{
    font-size:15px;
  font-family:Arial;
  width:100px;
  height:40px;
  border-width:1px;
  color:#fff;
  border-color:#d02718;
  font-weight:bold;
  border-top-left-radius:6px;
  border-top-right-radius:6px;
  border-bottom-left-radius:6px;
  border-bottom-right-radius:6px;
  box-shadow:inset 0px 1px 0px 0px #f5978e;
  text-shadow:inset 0px 1px 0px #810e05;
  background:linear-gradient(#f24537, #c62d1f);
}
.del-btn:hover{
    background: linear-gradient(#c62d1f, #f24537);
}
span.name,span.number{
    width: 50%;
    margin: 0 10px;
    
}

/*media queries */
@media screen and (max-width:800px) {
    .App{
        width: 70%;
    }
}
@media screen and (max-width:600px) {
    .App{
        width: 90%;
    }
}
@media screen and (max-width:350px) {
    .App{
        width: 100%;
        font-size: 12px;
    }
    .add-btn,.del-btn{}
}